function separator(input, options = { precision: 9 }) {
    try {
        if (!input) return ''
        input = '' + input
        /** Replace Non-last dots with comma **/
        input = input.replace(/\.(?=.*\.)/gi, ',')
        /** Define Flags **/
        const emptyFlag = input === ''
        const zeroEndFlag = !!input.match(/\.?0+$/gi)
        const dotEndFlag = !!input.match(/\.$/gi)
        /** Remove non-integer and non-dot chars **/
        input = input.replace(/[^0-9\\.]/g, '')
        if (zeroEndFlag) {
            const inputParts = input.split('.')
            inputParts[1] = inputParts[1]
                ? inputParts[1].slice(0, options.precision)
                : ''
            input =
                Number(inputParts[0]).toLocaleString('en-EG') +
                (inputParts.length && inputParts[1] ? '.' + inputParts[1] : '')
        } else {
            input = Number(input).toLocaleString('en-EG', {
                maximumFractionDigits: options.precision,
            })
            input = dotEndFlag ? input + '.' : input
        }
        return emptyFlag ? '' : input
    } catch (e) {
        console.log('numberFormatter got error')
        console.log(e)
        return input
    }
}
export {
    separator
}
